import Vue from 'vue'
import Router from 'vue-router'
import Srch from '@/components/Srch'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/srch/view'
    }, {
      path: '/srch/view',
      name: 'Srch',
      component: Srch
    }, {
      path: '*',
      redirect: '/'
    }
  ]
})



